import { InspectorControls } from "@wordpress/block-editor";
import { Fragment } from "@wordpress/element";
import { PanelBody, TextControl } from "@wordpress/components";

import savef from "./save.jsx";
import Sanitizer from "../sanitizing.js";
import defaultView, { defaultTitle, defaultPanel } from "../defaultview.js";

export default (props) => {
	const sanitizer = Sanitizer();
	const { __ } = wp.i18n;
	const { select, subscribe } = wp.data;
	const { useEffect } = wp.element;
	const { attributes, setAttributes } = props;
	props.editor = true;
	useEffect(() => {
		if (attributes.preview) return;
		const getBlockList = () =>
			select("core/block-editor")
				.getBlocks()
				.filter((b) => b.name === "core/heading");
		let blockList = getBlockList();
		const compareList = (x, y) => {
			for (var propertyName in x) {
				if (x[propertyName] !== y[propertyName]) {
					return true;
				}
			}
			return false;
		};
		setAttributes({ Headings: getBlocks(blockList) });
		subscribe(() => {
			const newBlockList = getBlockList();
			const blockListChanged = compareList(blockList, newBlockList);
			blockList = newBlockList;
			if (blockListChanged) {
				setAttributes({ Headings: getBlocks(blockList) });
			}
		});
	}, []); // Run effect with subscribe once

	const getLastTop = (array, n) => {
		if (n == 0) return array[0] != -1 ? array[0] : null;
		return array[n] != -1 ? array[n] : getLastTop(array, n - 1);
	};

	const getBlocks = (blocks) => {
		let lastHeading = [-1, -1, -1, -1, -1, -1];
		const headingArray = [];
		for (let i = 0; i < blocks.length; i++) {
			let parent = null;
			switch (blocks[i].attributes.level) {
				case 1:
					lastHeading = [i, -1, -1, -1, -1, -1];
					break;
				case 2:
					lastHeading[1] = i;
					parent = getLastTop(lastHeading, 0);
					break;
				case 3:
					lastHeading[2] = i;
					parent = getLastTop(lastHeading, 1);
					break;
				case 4:
					lastHeading[3] = i;
					parent = getLastTop(lastHeading, 2);
					break;
				case 5:
					lastHeading[4] = i;
					parent = getLastTop(lastHeading, 3);
					break;
				case 6:
					lastHeading[5] = i;
					parent = getLastTop(lastHeading, 4);
					break;
			}
			const heading = {
				level: blocks[i].attributes.level,
				content: blocks[i].attributes.content,
				anchor: blocks[i].attributes.anchor
					? blocks[i].attributes.anchor
					: null,
				parent: parent,
				children: [],
			};
			headingArray.push(heading);
			if (parent !== null) headingArray[parent].children.push(heading);
		}

		return headingArray.filter((h) => h.parent === null);
	};

	const onChangeUpdateTitle = (value) => {
		setAttributes({ title: value });
	};

	return (
		<Fragment>
			<InspectorControls>
				{defaultPanel()}
				<PanelBody title="Settings" initialOpen={true}>
					<TextControl
						label={__("Title", "sptcrb")}
						help={__("The title for your table of content", "sptcrb")}
						defaultValue={attributes.title}
						onChange={onChangeUpdateTitle}
					/>
				</PanelBody>
				<PanelBody title={defaultTitle} initialOpen={true}>
					{defaultView()}
				</PanelBody>
			</InspectorControls>
			{savef(props)}
		</Fragment>
	);
};
