import { escapeHTML, escapeAttribute } from "@wordpress/escape-html";

import { useBlockProps } from '@wordpress/block-editor';

export default (props) => {
	const { Headings, title } = props.attributes;


	const blockProps = props.editor ? useBlockProps({className: 'sptgb__toc__wrapper'}) : useBlockProps.save({className: 'sptgb__toc__wrapper'});

	const headingList = (headings) => {
		return (
			<ul>
				{headings.map((heading, index) => {
					return (
						<li key={index} className="sptgb__toc__wrapper__content__headline">
							{headingWrapper(heading)}
							{handleChildren(heading)}
						</li>
					);
				})}
			</ul>
		);
	};

	const headingWrapper = (element) => {
		if (element.anchor) {
			return (
				<a
					{...(element.anchor
						? { href: "#" + escapeAttribute(element.anchor) }
						: {})}
				>
					{escapeHTML(element.content)}
				</a>
			);
		}
		return <span>{escapeHTML(element.content)}</span>;
	};

	const handleChildren = (heading) => {
		if (heading.children.length === 0) return;

		return headingList(heading.children);
	};

	return (
		<div {...blockProps}>
			<div className="sptgb__toc__wrapper__header">
				<span className="sptgb__toc__wrapper__header__headline">
					{escapeHTML(title)}
				</span>
			</div>
			<div className="sptgb__toc__wrapper__content">
				{headingList(Headings)}
			</div>
		</div>
	);
};
